.button {
    padding: 5px 24px;
    border-radius: 3px;
    border: none;

    font-family: 'Montserrat', sans-serif;
    line-height: 20px;
    font-size: 14px;
    font-weight: 600;
}

.disabled {
    opacity: 0.5;
    background-color: #004D99;
}

.secondary {
    background-color: #FFFFFF;
    border: 1px solid #0066CC;
    color: #0066CC;
}

.default {
    color: #FFFFFF;
    background-color: #0066CC;
}

.red {
    color: #FFFFFF;
    background-color: #CC1439;
}

.default:hover {
    background-color: #0080FF;
}

.red:hover {
    background-color: #FF1A47;
}

.secondary:hover {
    background-color: #E5F0FA;
}


.default:active {
    background-color: #0059B2;
}

.secondary:active {
    background-color: #C9E5FF ;
}

.red:active {
    background-color: #B21232;
}

